import React from "react";
import { alertType } from "../../../config";
import "./Alert.css";

const Alerts = ({ type, title, message }) => {
  const [showAlert, setShowAlert] = React.useState(true);
  let typeColor = "";
  let crossColor = "";
  switch (type) {
    case alertType.INFO:
      typeColor = "bg-blue border-blue text-blue";
      crossColor = "text-blue";
      break;
    case alertType.WARNING:
      typeColor = "bg-yellow border-yellow text-yellow";
      crossColor = "text-yellow";
      break;
    case alertType.ERROR:
      typeColor = "bg-red border-red text-red";
      crossColor = "text-red";
      break;
    case alertType.SUCCESS:
      typeColor = "bg-green border-green text-green";
      crossColor = "text-green";
      break;
    default:
  }

  React.useEffect(() => {
    let timeoutID = null;
    if (showAlert) {
      timeoutID = setTimeout(() => {
        setShowAlert(false);
      }, 3000);
    }

    return () => {
      timeoutID !== null && clearTimeout(timeoutID);
    };
  }, [showAlert]);

  if (!showAlert) {
    return null;
  }
  return (
    <>
      <div className={`${typeColor} alertContainer relative`} role="alert">
        {title ? <p className="titleBold">{title}</p> : ""}
        <p className="alertMsg">
          {message ? message : "Something went wrong."}
        </p>
        <span
          className="absolute crossContainer"
          onClick={() => setShowAlert(false)}
        >
          <svg
            className={`fill-current icn ${crossColor}`}
            role="button"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 20 20"
          >
            <title>Close</title>
            <path d="M14.348 14.849a1.2 1.2 0 0 1-1.697 0L10 11.819l-2.651 3.029a1.2 1.2 0 1 1-1.697-1.697l2.758-3.15-2.759-3.152a1.2 1.2 0 1 1 1.697-1.697L10 8.183l2.651-3.031a1.2 1.2 0 1 1 1.697 1.697l-2.758 3.152 2.758 3.15a1.2 1.2 0 0 1 0 1.698z" />
          </svg>
        </span>
      </div>
    </>
  );
};

export default Alerts;
