import peaks from "../../../assets/images/Peaks.png";
import NewsListHeader from "./news-header/NewsListHeader";
import NewsItem from "./news-tems/NewsItem";
import styles from "./NewsList.module.css";

function NewsList({ newsList, isNewsListHeader = false, grid, pageTitle }) {
  return (
    <>
      <div className={styles.container}>
        <div className={styles.wrapperNewsList}>
          {isNewsListHeader ? <NewsListHeader pageTitle={pageTitle} /> : ""}
          <div className={grid}>
            {newsList.map((newsList, idx) => {
              const imgSrc = newsList?.fields?.thumbnail ?? newsList?.thumbnail;
              return (
                <NewsItem
                  key={newsList.id}
                  id={newsList.id}
                  sectionId={newsList.sectionId}
                  title={newsList.webTitle}
                  image={imgSrc ?? peaks}
                  isNewsListHeader={isNewsListHeader}
                  topNewsIdx={isNewsListHeader ? idx : null}
                />
              );
            })}
          </div>
        </div>
      </div>
    </>
  );
}

export default NewsList;
