export const useChooseSection = (sectionID) => {
  let borderType;
  switch (sectionID) {
    case "sport":
      borderType = "newsCard-border-red";
      break;
    case "culture":
      borderType = "newsCard-border-yellow";
      break;
    case "lifeandstyle":
      borderType = "newsCard-border-blue";
      break;
    default:
      borderType = "newsCard-border-green";
  }
  return borderType;
};
