import React from "react";
import { Loader } from "../../atoms";
import NewsList from "../news/NewsList";

const SearchResult = ({
  isLoading,
  isError,
  error,
  data,
  isNewsListHeader = false,
  pageTitle,
  grid,
}) => {
  let content;
  if (isLoading) content = <Loader />;
  else if (isError)
    content = <div> Something went wrong : {error.message} </div>;
  else {
    content = (
      <>
        {data.length > 0 && (
          <NewsList
            newsList={data}
            isNewsListHeader={isNewsListHeader}
            pageTitle={pageTitle}
            grid={grid}
          />
        )}
      </>
    );
  }
  return <>{content}</>;
};
export default SearchResult;
