import React from "react";
import "./Dropdown.css";
const Dropdown = ({ title, content, onSelection, selectedItem }) => {
  return (
    <div className="select-dropdown">
      <select
        value={selectedItem}
        onChange={(e) => onSelection(e.target.value)}
      >
        {content.length > 0 &&
          content.map((item, index) => {
            return (
              <option
                key={`${item.id}_${item.name}`}
                value={item.value}
                onClick={() => onSelection(item.value)}
              >
                {item.name}
              </option>
            );
          })}
      </select>
    </div>
  );
  return (
    <div className="dropdown" style={{ float: "right" }}>
      <button className="dropbtn">{title}</button>
      <div className="dropdown-content">
        {content.length > 0 &&
          content.map((item, index) => {
            return (
              <div key={`${item.id}_${item.name}`} onClick={() => onSelection}>
                {item.name}
              </div>
            );
          })}
      </div>
    </div>
  );
};

export default Dropdown;
