import { apiSectionFilters } from "../../../../config";
import { useGetNews } from "../../../../hooks/useReactQuery";
import { ResultContainer } from "../../../atoms";
import Result from "../../result/Result";

const RANDOM_NEWS_END_POINT = "search";

const RandomNews = () => {
  const queryParams = {
    "show-fields": apiSectionFilters.SHOW_ALL,
    "order-by": apiSectionFilters.ORDER_BY_RELEVANCE,
    "page-size": 3,
  };

  // const {
  //   isLoading,
  //   isError,
  //   error,
  //   data: randomNews,
  // } = useQuery(
  //   [
  //     "randomNews",
  //     queryParams["show-fields"],
  //     queryParams["order-by"],
  //     queryParams["page-size"],
  //   ],
  //   () => getNews(RANDOM_NEWS_END_POINT, queryParams)
  // );

  const keys = [
    "randomNews",
    queryParams["show-fields"],
    queryParams["order-by"],
    queryParams["page-size"],
  ];
  const optionObject = {
    queryKey: [...keys],
    queryEndPoint: RANDOM_NEWS_END_POINT,
    queryParams,
  };

  const {
    isLoading,
    isError,
    error,
    data: randomNews,
  } = useGetNews(optionObject);

  const isRandomNewsExists = randomNews?.data?.response?.results?.length > 0;
  const newsSections =
    isRandomNewsExists &&
    new Set(
      randomNews?.data?.response?.results.reduce((accumulator, currentVal) => {
        return [...accumulator, currentVal.sectionName];
      }, [])
    );

  return (
    <>
      <ResultContainer parentDiv={"container wrapperNewsList mAuto"}>
        <div className="mbHalf ml40p">
          <h3>
            {newsSections.size > 1
              ? Array.from(newsSections).slice(0, 3).join(" | ")
              : Array.from(newsSections).join(" | ")}
          </h3>
        </div>
        <Result
          isLoading={isLoading}
          isError={isError}
          error={error}
          data={randomNews?.data?.response?.results}
          grid={"grid-3"}
        />
      </ResultContainer>
    </>
  );
};

export default RandomNews;
