import React from "react";
import { BsFillBookmarkFill } from "react-icons/bs";
import { useNavigate } from "react-router-dom";
import { apiSectionFilters } from "../../../config";
import { navigationPath } from "../../../config/navigationList";
import { SORT_BY } from "../../../store/slices/newsSortSlice";
import { useAppDispatch, useAppSelector } from "../../../store/store";
import { Button, Dropdown } from "../../atoms";
import styles from "./NewsFilter.module.css";

const NewsFilter = () => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const bookmarkCount = useAppSelector((s) => s.bookmark.length);
  const orderBy = useAppSelector((s) => s.sort);

  const dropDownContent = [
    { id: 1, name: "Newest", value: apiSectionFilters.ORDER_BY_NEW },
    { id: 2, name: "Oldest", value: apiSectionFilters.ORDER_BY_OLD },
    { id: 3, name: "Relevance", value: apiSectionFilters.ORDER_BY_RELEVANCE },
  ];
  const sortHandler = async (value) => {
    await dispatch(SORT_BY(value));
  };
  const showBookmarkBtn = bookmarkCount > 0 ? true : false;

  return (
    <div className={styles.wrapper}>
      <div>
        <Button
          classes={
            showBookmarkBtn
              ? "bookmarkedBtn enableBtn pointer"
              : "bookmarkedBtn disabledBtn"
          }
          onClick={() => navigate(navigationPath.BOOKMARK_PAGE)}
          showBookmarkBtn={Boolean(showBookmarkBtn)}
        >
          <BsFillBookmarkFill size={11} className="bookMarkIcn" />
          <span>VIEW BOOKMARK ({bookmarkCount}) </span>
        </Button>
      </div>
      <div className={styles.dropDownContainer}>
        <Dropdown
          title={"Sort By"}
          onSelection={sortHandler}
          content={dropDownContent}
          selectedItem={orderBy}
        />
      </div>
    </div>
  );
};
export default NewsFilter;
