import React, { useState } from "react";
import { BsFillBookmarkFill } from "react-icons/bs";
import { useQuery } from "react-query";
import { useParams } from "react-router-dom";
import { AlertWrapper, Button, Loader } from "../../components";
import { alertType, apiSectionFilters } from "../../config";
import { getNews } from "../../service/section.service";
import {
  ADD_TO_BOOKMARK,
  REMOVE_FROM_BOOKMARK,
} from "../../store/slices/bookmarkSlice";
import { useAppDispatch, useAppSelector } from "../../store/store";
import styles from "./NewsDetails.module.css";

const NewsDetails = () => {
  const param = useParams();
  const newsIdParam = param["*"];
  const dispatch = useAppDispatch();
  const bookmarkList = useAppSelector((s) => s.bookmark);
  const [alert, setAlert] = useState(false);
  const [chooseAlertType, setChooseAlertType] = useState("");

  const queryParams = {
    "show-fields": apiSectionFilters.SHOW_ALL,
  };
  const {
    isLoading,
    isError,
    error,
    data: newsDetail,
  } = useQuery(["news-details"], () => getNews(newsIdParam, queryParams), {
    select: (res) => {
      return res?.data?.response?.content;
    },
  });

  // const keys = ["news-details"];
  // const optionObject = {
  //   queryKey: [...keys],
  //   queryEndPoint: newsIdParam,
  //   queryParams,
  //   // select: (res) => {
  //   //   return res?.data?.response?.content;
  //   // },
  // };

  // const {
  //   // isLoading,
  //   // isError,
  //   // error,
  //   data: newsDetail2,
  // } = useGetNews(optionObject);
  const addToBookMark = async () => {
    const {
      id,
      webTitle,
      fields: { thumbnail },
    } = newsDetail;
    await dispatch(ADD_TO_BOOKMARK({ id, webTitle, thumbnail }));
    setAlert(true);
    setChooseAlertType(alertType.SUCCESS);
  };

  const removeFromBookmark = async () => {
    await dispatch(REMOVE_FROM_BOOKMARK(newsIdParam));
    setAlert(true);
    setChooseAlertType(alertType.ERROR);
  };

  const handleBookmark = async (isBookmark) => {
    (await isBookmark) ? removeFromBookmark() : addToBookMark();
    setAlert(false);
    setChooseAlertType("");
  };

  if (isLoading) return <Loader />;
  else if (isError) return <div> Something went wrong : {error.message} </div>;
  else {
    const isDetailExists = newsDetail.length > 0;
    const getBookmark =
      bookmarkList && bookmarkList.find((el) => el.id === newsIdParam);
    const isBookmarked = getBookmark === undefined ? false : true;

    const defineAlert =
      chooseAlertType === alertType.SUCCESS
        ? alertType.SUCCESS
        : alertType.ERROR;
    const alertTitle =
      chooseAlertType === alertType.SUCCESS ? "Bookmarked" : "Remove Bookmark";
    const alertMsg =
      chooseAlertType === alertType.SUCCESS
        ? "successfully added to bookmark."
        : "remove from bookmark.";

    return (
      <div className="container mtAftrNav">
        <div style={{ minHeight: "700px" }}>
          {alert && (
            <AlertWrapper
              type={defineAlert}
              title={alertTitle}
              message={`"${newsDetail.webTitle}" : ${alertMsg}`}
            />
          )}
          <div className={`${styles.wrapperNewsList} ${styles.gap20}`}>
            <Button
              classes={"bookmarkedBtn enableBtn pointer"}
              onClick={() => handleBookmark(isBookmarked)}
            >
              <BsFillBookmarkFill size={11} className="bookMarkIcn" />
              <span>{isBookmarked ? "REMOVE" : "ADD"} BOOKMARK</span>
            </Button>
            <div className={styles.text11Px}>
              {newsDetail.webPublicationDate}
            </div>
            <div>
              <div className={styles.contentWrapper}>
                <div
                  className={`${styles.wrapperNewsList} ${styles.conentWidth} ${styles.gap20}`}
                >
                  <h1>{newsDetail.webTitle}</h1>
                  <h3>{newsDetail?.fields?.headline ?? null}</h3>
                  <hr className={styles.bgLightGray} />
                  <div
                    dangerouslySetInnerHTML={{
                      __html: newsDetail?.fields?.body,
                    }}
                  />
                </div>
                <div className={styles.imgContainer}>
                  <img
                    className={styles.articleImg}
                    src={newsDetail.fields.thumbnail}
                    alt={newsDetail.webTitle}
                  />
                  <p className={`${styles.trailText} ${styles.text11Px}`}>
                    <div
                      dangerouslySetInnerHTML={{
                        __html: newsDetail?.fields?.trailText,
                      }}
                    />
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
};

export default NewsDetails;
