import React from "react";
import { apiSectionFilters } from "../../../../config";
import { useGetNews } from "../../../../hooks/useReactQuery";
import { useAppSelector } from "../../../../store/store";
import { ResultContainer } from "../../../atoms";
import Result from "../../result/Result";

const TOP_NEWS_END_POINT = "search";

const TopNews = () => {
  const orderBy = useAppSelector((s) => s.sort);
  const queryParams = {
    sections: apiSectionFilters.NEWS_SECTION,
    // q: apiSectionFilters.NEWS_SECTION,
    "show-fields": apiSectionFilters.SHOW_THUMBNAIL,
    "order-by": orderBy,
    // "order-by": apiSectionFilters.ORDER_BY_NEW,
    "page-size": 8,
  };

  const keys = [
    "top_news",
    queryParams.sections,
    queryParams["show-fields"],
    queryParams["page-size"],
    queryParams["order-by"],
  ];
  const optionObject = {
    queryKey: [...keys],
    queryEndPoint: TOP_NEWS_END_POINT,
    queryParams,
    otherQueryConfig: "",
  };

  const { isLoading, isError, error, data: topNews } = useGetNews(optionObject);
  return (
    <>
      <ResultContainer parentDiv={"container mtAftrNav"} childDiv={"minH7"}>
        <Result
          isLoading={isLoading}
          isError={isError}
          error={error}
          data={topNews?.data?.response?.results.slice(0, 5)}
          isNewsListHeader={true}
          pageTitle={"Top Stories"}
          grid={"grid-4 newsGrid"}
        />
        <div className="tNewsSectionTwo">
          <Result
            isLoading={isLoading}
            isError={isError}
            error={error}
            data={topNews?.data?.response?.results.slice(-3)}
            grid={"tNewsSectionTwo-3"}
          />
        </div>
      </ResultContainer>
    </>
  );
};

export default TopNews;
