import React, { useEffect } from "react";
import { useInfiniteQuery } from "react-query";
import { useParams } from "react-router-dom";
import {
  Heading,
  Loader,
  NewsFilter,
  Result,
  ResultContainer,
} from "../../components";
import { useResetSort } from "../../hooks/useRestSort";
import { getNewsBySearch } from "../../service/section.service";
import { useAppSelector } from "../../store/store";

const NewsSearch = () => {
  useResetSort();
  const { q: searchTerm } = useParams();
  const orderBy = useAppSelector((s) => s.sort);
  const queryString = {
    pageParam: 1,
  };
  const {
    isLoading,
    isError,
    error,
    data: newsSearchRes,
    hasNextPage,
    fetchNextPage,
  } = useInfiniteQuery(
    [searchTerm, orderBy],
    (queryString) => getNewsBySearch(queryString),
    {
      getNextPageParam: (response) => {
        const maxPage = response.response.pages;
        const nextPage = response.response.currentPage + 1;
        return nextPage <= maxPage ? nextPage : undefined;
      },
    }
  );

  useEffect(() => {
    let fetching = false;
    const onScroll = async (event) => {
      // scrollHeight = scrollTop + clientHeight
      const { scrollHeight, scrollTop, clientHeight } =
        event.target.scrollingElement;

      if (!fetching && scrollHeight - scrollTop <= clientHeight * 1.5) {
        fetching = true;
        if (hasNextPage) {
          await fetchNextPage();
        }
        fetching = false;
      }
    };
    document.addEventListener("scroll", onScroll);
    return () => {
      document.removeEventListener("scroll", onScroll);
    };
  }, [fetchNextPage, hasNextPage]);

  let content;
  if (isLoading) content = <Loader />;
  else if (isError)
    content = <div> Something went wrong : {error.message} </div>;
  else {
    const isPages = newsSearchRes?.pages.length > 0;
    content = (
      <>
        {isPages &&
          newsSearchRes?.pages.map((page, index) => (
            <Result
              key={`${index}`}
              isLoading={isLoading}
              isError={isError}
              error={error}
              data={page?.response?.results}
              grid={"tNewsSectionTwo-3"}
            />
          ))}
      </>
    );
  }
  return (
    <ResultContainer parentDiv={"container mtAftrNav"}>
      <div className="minH7 wrapperNewsList">
        <div className="mNewSearchTitle newsSearchHeader">
          <Heading> Search result : {searchTerm} </Heading>
          <NewsFilter />
        </div>
        {content}
      </div>
    </ResultContainer>
  );
};

export default NewsSearch;
