import React, { useEffect, useRef, useState } from "react";
import { AiOutlineSearch } from "react-icons/ai";
import { useNavigate } from "react-router-dom";

const NavList = ({ isOpenNav }) => {
  const navigate = useNavigate();
  const wrapperRef = useRef(null);

  const [isFocus, setIsFocus] = useState(false);
  useEffect(() => {
    document.addEventListener("click", handleClickOutside, false);
    return () => {
      document.removeEventListener("click", handleClickOutside, false);
    };
  }, []);

  const handleClickOutside = (event) => {
    if (wrapperRef.current && !wrapperRef.current.contains(event.target)) {
      setIsFocus(false);
    }
  };

  function submitHandler(event) {
    event.preventDefault();
    const searchTerm = wrapperRef.current.value;
    if (searchTerm.trim().length !== 0) {
      navigate(`/news-search/${searchTerm}`, { replace: true });
      wrapperRef.current.value = "";
      isOpenNav(false);
    } else {
      alert("Please enter search term...");
    }
  }
  return (
    <>
      <li className="relative navSearch">
        <div className="absolute searchBoxPosition">
          <form onSubmit={submitHandler} className="searchContainer">
            <input
              type="search"
              placeholder={`${isFocus ? "Search all news" : ""}`}
              className={`searchBox search ${isFocus ? "searchFocus" : ""}`}
              ref={wrapperRef}
              name="searchNews"
            />
            <button className={`searchBtn pointer fixed`}>
              <AiOutlineSearch
                size={25}
                style={{ marginTop: "5px" }}
                className="searchIcon"
                onMouseEnter={(e) => setIsFocus(true)}
                // color={"#ffffff"}
              />
            </button>
          </form>
        </div>
      </li>
    </>
  );
};

export default NavList;
