import React from "react";
import { useAppSelector } from "../../../store/store";
import { Heading } from "../../atoms";
import NewsList from "../news/NewsList";

const ViewBookmark = () => {
  const bookmarkList = useAppSelector((s) => s.bookmark);
  return (
    <>
      <div className="container wrapperNewsList mtAftrNav">
        <div className="wrapperNewsList">
          <div style={{ marginBottom: "1.5%" }}>
            <Heading> All Bookmark </Heading>
          </div>
          {bookmarkList.length > 0 && (
            <div style={{ marginLeft: "-4%" }}>
              <NewsList newsList={bookmarkList} grid={"tNewsSectionTwo-3"} />
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default ViewBookmark;
