import { apiSectionFilters } from "../../config/";
const { createSlice } = require("@reduxjs/toolkit");

const initialState = apiSectionFilters.ORDER_BY_NEW;

const newsSortSlice = createSlice({
  name: "newsSort",
  initialState,
  reducers: {
    SORT_BY(state, action) {
      return action.payload;
      state = action.payload;
      // return [...state, action.payload];
    },
    // reset: () => initialState
  },
});

export const { SORT_BY } = newsSortSlice.actions;
// export const { SORT_BY, reset } = newsSortSlice.actions;
export default newsSortSlice.reducer;
