const { createSlice } = require("@reduxjs/toolkit");

const initialState = [];

const bookmarkSlice = createSlice({
  name: "bookmark",
  initialState,
  reducers: {
    ADD_TO_BOOKMARK(state, action) {
      return [...state, action.payload];
    },
    REMOVE_FROM_BOOKMARK(state, action) {
      return state.filter((news) => news.id !== action.payload);
    },
  },
});

export const { ADD_TO_BOOKMARK, REMOVE_FROM_BOOKMARK } = bookmarkSlice.actions;
export default bookmarkSlice.reducer;
