import { useNavigate } from "react-router-dom";
import { useChooseSection } from "../../../../hooks/useChooseSectionId";
import { Card } from "../../../atoms";
import styles from "./NewsItem.module.css";

function NewsItem(props) {
  const navigate = useNavigate();
  const { topNewsIdx, isNewsListHeader, sectionId } = props;
  const topNewsMap = topNewsIdx && topNewsIdx !== 0;

  const showDetailsHandler = (newsId) => {
    navigate(`/news-detail/${newsId}`, { replace: true });
  };

  const containerClasses = isNewsListHeader
    ? !topNewsMap
      ? " firstTopNewsChild "
      : " nGridItem "
    : "";

  const cardWrapper = isNewsListHeader
    ? !topNewsMap
      ? " "
      : " topNewsCard "
    : " randomNew ";

  const imgWrapper = isNewsListHeader
    ? !topNewsMap
      ? " firstImgCrd"
      : " othImgCrd "
    : " randomImgWrap ";

  const contantWrapper = isNewsListHeader
    ? !topNewsMap
      ? " "
      : " topNewsCardContent "
    : " randomNewCardContent ";

  const chooseSectionColor = useChooseSection(sectionId);
  return (
    <div
      className={`${styles.item} ${containerClasses} pointer`}
      onClick={() => showDetailsHandler(props.id)}
    >
      <div className={`${cardWrapper}`}>
        <Card>
          <div className={`${styles.image} ${imgWrapper}`}>
            <img src={props.image} alt={props.title} />
          </div>
          <div
            className={`${styles.contentWrapper} ${contantWrapper} ${chooseSectionColor}`}
          >
            <div className={styles.content}>
              <div>{props.title}</div>
            </div>
          </div>
        </Card>
      </div>
    </div>
  );
}

export default NewsItem;
