import { configConst, httpClient } from "../config/";

// const BASE_URL = "https://content.guardianapis.com/";

//https://content.guardianapis.com/search?section=news&order-by=newest&rights=developer-community&api-key=test

const API_KEY = configConst.API_KEY;

// https://content.guardianapis.com/football/live/2023/feb/03/weekend-football-countdown-premier-league-team-news?api-key=a6f98458-f394-46c8-941a-392761ab8a04&show-fields=all

export const getNews = async (endPoint, queryParam) => {
  const response = await httpClient.get(`${endPoint}`, {
    params: {
      ...queryParam,
      // "api-key": API_KEY,
      "api-key": "a6f98458-f394-46c8-941a-392761ab8a04",
    },
  });
  return response;
};

export const getNewsBySearch = async ({ pageParam = 1, queryKey }) => {
  try {
    return await (
      await fetch(
        `https://content.guardianapis.com/search?q=${queryKey[0]}&page-size=15&order-by=${queryKey[1]}&show-fields=all&page=${pageParam}&api-key=a6f98458-f394-46c8-941a-392761ab8a04`
      )
    ).json();
  } catch (err) {
    throw new Error(err);
  }
};

// Back up for infinite query
export const _getNews = async ({ pageParam = 1, queryKey }) => {
  try {
    return await (
      await fetch(
        `https://content.guardianapis.com/search?q=${queryKey[0]}&page-size=15&order-by=relevance&show-fields=all&page=${pageParam}&api-key=a6f98458-f394-46c8-941a-392761ab8a04`
      )
    ).json();
  } catch (err) {
    throw new Error(err);
  }
};
